.bm-menu-wrap {
  background-color: red !important;
}

.bm-menu-wrap {
    width: 100% !important;
  }
  
  .bm-cross:hover span {
    background: #ffffff ;
  }
  
  .bm-cross-button button {
      left: -36px !important;
      top: 23px !important;
      width: 112% !important;
      height: 151% !important;
      outline: none;
  }
  
  .bm-item {
    outline: none;
    display: inline-block;
    text-decoration: none;
    margin: 30px;
    color: #d1d1d1;
    font-size: 40px;
    font-weight: bold;
    transition: .3s linear ;
    padding: 12px 60px;
    text-transform: uppercase;
    position: relative;
    z-index: 0;
  }
  
  .bm-item:hover {
    color: #000;
  }

  .bm-item::before,
  .bm-item::after{
    content: "";
    position: absolute;
    width: 100%;
    height: 0%;
    left: 0;
    z-index: -1;
  }

  .menu-item::before{
    bottom: 0;
    border-left: 1px solid #ffffff;
    border-right: 1px solid #ffffff;
    transition: .3s linear;
  }

  .menu-item::after{
    top: 0;
    left: 1px;
    background-color:#ffffff ;
    transition: .3s linear .3s;
  }

  .menu-item:hover::before,
  .menu-item:hover::after{
    height: 100%;
  }
  
  .bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    right: 36px;
    top: 36px;
  }
  
  .bm-burger-button button {
    outline: none;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #ffffff;
    height: 15% !important;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
     background: ivory !important;
     right: 30px;
     top: 22px;
     width: 6px !important;
     height: 30px !important;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #000;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }

.Header{
    height: 100vh;
    min-height: 500px;
    background: url(/MyPortfolio/static/media/header.9b8aab4f.jpg) no-repeat center;
    background-size: cover;
    background-attachment: fixed;
}

.Header:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,.5);
    z-index: 2;
  }

.Contacts__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    text-align: center;
    position: relative;
    z-index: 3;

}

.Contacts__content {
    width: 100%;
    color: #ffffff;
}

.Contacts__wrapper h1 {
    font-size: 60px;
    margin-bottom: 60px;
}

.Contacts__wrapper h1::after {
    content: "Hi";
    -webkit-animation: textanim 10s linear infinite;
            animation: textanim 10s linear infinite;
}

@-webkit-keyframes textanim {
    25%{
        content: "i`m Demian Vytrykush";
    }
    50%{
        content: "and It`s my";
    }
    75%{
        content: "Portfolio Page";
    }
}

@keyframes textanim {
    25%{
        content: "i`m Demian Vytrykush";
    }
    50%{
        content: "and It`s my";
    }
    75%{
        content: "Portfolio Page";
    }
}

.Contact__icons a {
    width: 60px;
    height: 60px;
    background-color: #ffffff;
    display: inline-block;
    border-radius: 20px;
    box-shadow: 6px 6px 12px rgba(0,0,0,.15),
                -6px -6px 12px rgba(255,255,255,.2);
    color: black;
    font-size: 28px;
    margin: 0 20px;
    transition: .3s linear;
    overflow: hidden;
    position: relative;
}

.Contact__icons a:hover {
    transform: scale(1.2);
    border-radius: 50%;
    color: black;
}

.Contact__icons a::before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.Contact__buttons {
    margin-top: 60px;
}

.Contact__buttons a {
    display: inline-block;
    margin: 15px 30px;
    color: #ffffff;
    font-size: 15px;
    font-weight: 600;
    width: 180px;
    border: 1px solid #ffffff;
    padding: 14px 0;
    border-radius: 6px;
    transition: .2s linear;
    text-decoration: none;
}

.Contact__buttons a:hover,
.Contact__buttons a:nth-child(2) {
    background-color: #ffffff;
    color: black;
}


.About {
    padding: 100px 30px;
    background-color: #f1f1f1;
}

.About__wrapper {
    max-width: 1300px;
    margin: auto;
    padding: 0 40px;
}

.About__wrapper h1 {
    text-align: center;
    margin-bottom: 80px;
    position: relative;
    font-size: 26px;
    padding-bottom: 20px;
    color: #111111;
}

.About__wrapper h1::before {
    content: "";
    position: absolute;
    width: 80px;
    height: 3px;
    background-color: #111111;
    bottom: 0;
    left: calc(50% - 40px);
}

.About__wrapper h1::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 10px;
    background-color: #111111;
    border: 4px solid #f1f1f1;
    left: calc(50% - 12px);
    bottom:  -7px;
}

.About__content {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.About__content img {
    width: 200px;
    border-radius: 50%;
    margin-right: 100px;
    transition: .3s linear;
}

.About__text {
    flex: 1 1;
}

.About__text h3 {
    margin: 10px 0;
    color: #444444;
    font-size: 14px;
}

.About__text h3 span::before {
    content: "";
    width: 6px;
    height: 6px;
    background-color: #444444;
    display: inline-block;
    border-radius: 50%;
    margin: 3px 6px;
}

.About__text p {
    font-size: 17px;
    text-align: justify;
    line-height: 26px;
    margin-top: 20px;
}

.Skills {
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.Skill {
    width: calc(50% - 20px);
    margin: 15px 0;
}

.Skill__info {
    display: flex;
    justify-content: space-between;
}

.skill-bar {
    height: 3px;
    background-color: #dddddd;
    margin-top: 14px;
    position: relative;
}

.skill-bar::after {
    content: "";
    position: absolute;
    height: 6px;
    background-color: #1f1f1f;
    bottom: 0;
    transition: 2s linear;

}

.React::after{
    width: 75%;
    -webkit-animation: React 3s;
            animation: React 3s;
}

.JavaScript::after{
    width: 70%;
    -webkit-animation: JavaScript 3s;
            animation: JavaScript 3s;
}

.HTML::after{
    width: 90%;
    -webkit-animation: HTML 3s;
            animation: HTML 3s;

}

.CSS::after{
    width: 80%;
    -webkit-animation: CSS 3s;
            animation: CSS 3s;
}

@-webkit-keyframes React {
    0%{
        width: 0%;
    }
    100%{
        width: 75%;
    }
}

@keyframes React {
    0%{
        width: 0%;
    }
    100%{
        width: 75%;
    }
}

@-webkit-keyframes JavaScript {
    0%{
        width: 0%;
    }
    100%{
        width: 70%;
    }
}

@keyframes JavaScript {
    0%{
        width: 0%;
    }
    100%{
        width: 70%;
    }
}

@-webkit-keyframes HTML {
    0%{
        width: 0%;
    }
    100%{
        width: 90%;
    }
}

@keyframes HTML {
    0%{
        width: 0%;
    }
    100%{
        width: 90%;
    }
}

@-webkit-keyframes CSS {
    0%{
        width: 0%;
    }
    100%{
        width: 80%;
    }
}

@keyframes CSS {
    0%{
        width: 0%;
    }
    100%{
        width: 80%;
    }
}


@media  screen and (max-width: 900px){
    .About__content img {
        margin: 0 auto 60px;
    }

    .About__text {
        flex: 100% 1;
        text-align: center;
    }

    .bm-item {
        font-size: 30px;
        text-align: center;
    }
}

@media  screen and (max-width: 600px){
    .Skill {
        width: 100%;
    }

    .Contacts__wrapper h1 {
        font-size: 40px;
    }
}


.Education {
    background-color: rgb(248, 248, 252);
}

.Education__wrapper {
