}

.Education__wrapper h1 {
    text-align: center;
    margin-bottom: 80px;
    position: relative;
    font-size: 26px;
    padding-bottom: 20px;
    color: #111111;
}

.Education__wrapper h1::before {
    content: "";
    position: absolute;
    width: 80px;
    height: 3px;
    background-color: #111111;
    bottom: 0;
    left: calc(50% - 40px);
}

.Education__wrapper h1::after {
    content: "";
