    width: 16px;
    height: 10px;
    background-color: #111111;
    border: 4px solid rgb(248, 248, 252);
    left: calc(50% - 12px);
    bottom:  -7px;
}

.Education__time-line {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.Education__block {
    width: calc(43% - 20px);
    background-color: #f1f1f1;
    border: 1px solid #dddddd;
    margin: 10px 0;
    padding: 30px;
    position: relative;
}

.Education__block::before{
    content: "";
    position: absolute;
    width: 1px;
    height: 120%;
    background-color: #dddddd;
    left: -20px;
    top: 50%;
    transform:translateY(-50%)
}

.Education__block::after{
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: #111111;
    left: -28px;
    top: 30px;
}

.Education__block h3 {
    font-size: 16px;
    margin: 10px 0;
}

.Education__block p {
    font-size: 15px;
    color: #444444;
}

@media  screen and (max-width: 900px){
    .Education__block {
        width: calc(100% - 20px);
        margin-left: auto;
    }
}

